* {
    font-family:  'Poppins', sans-serif;
}
h1{
    font-family: 'Rubik Moonrocks', cursive;
    color: cadetblue;
    text-align: center;
    font-size: 60px;
}
.content-cards{
    width: 20%;
    border: 1px solid rgba(0, 0, 0, 0.247);
    border-radius: 15px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.247), 0 5px 5px 0 rgba(0, 0, 0, 0.247); 
    margin: 30px;
    max-height: 10%;
    padding: 20px;
    text-align: center;
}
.content-cards:hover{
    opacity: 0.75;
    box-shadow: 0 0 20px 0 cadetblue, 0 5px 5px 0 cadetblue; 
    cursor:grab;
}
.content-cards img{
    max-width: 100%;
}
.cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
input{
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    padding: 5px;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24); 
    text-align: center;
}
.form{
    text-align: center;
}